@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200;300;400;500;600;700;800;900&family=Roboto:wght@700;900&display=swap');
*{
  box-sizing:border-box;

}

html, body {
  max-width: 100%;
  overflow-x: hidden;
  }
  
body {
  margin: 0;
  font-family:   'Poppins', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

    display: flex;
    align-items: center;
    flex-direction: column;
    margin: 0;
    background-color: white;

    overflow-x: hidden;
    overflow-y: auto;

}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
